*{
    font-family: 'Poppins', sans-serif;
    font-family: 'Lustria', serif;
}


@media (min-width: 992px){
    .container {
        max-width: 900px;
    }
}

#db-logout {
    background: thistle;
}

#db-logout:hover {
    background: #ded9e3;
}

.img-fluid, .img-thumbnail {
    width: 330px;

}
#react-paginate ul {
    display: inline-block;
    padding-left: 15px;
    padding-right: 15px;
}

#react-paginate li {
    display: inline-block;
}
div#project-comments ul {
    padding: 0;
}

.pagination{
    padding-left: 2.2rem;
}


.pagination ul li, ul.pagination li {
    margin: 0 auto !important;
}

.pagination li a {
    line-height: 2rem;
    font-size: 1.3rem;
}

.pagination li.active a, .pagination li.active{
    color: #000000;
    cursor: text;
    font-weight: 600;
}

.pagination li a{
    color: blue;
    cursor: pointer;
}

.pagination .disabled a, .pagination .disabled {
    color: gray;
    cursor:text;
}

label.custom-file-label {
    overflow: hidden;
}

.loader {
    height: 200px;
    background: rgba(170, 149, 149, 0.2);
    width: 100%;
    position: fixed;
    top: 0;
    height: 100%;
    display: block;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 2;
}
